.about-title{
    text-align: center;
    color: #4b1827;
}

.about-us-container {
    padding: 2% 5% 0%;
    text-align: center;
    color: #4b1827;
}

.about-img {
    width: 100%;
    aspect-ratio: 16/9;
    border-radius: 10px;
    padding: 3% 0 0;
}
.about-text{
    padding: 4% 3% 2%;
}

.about-para{
    font-size: 16px !important;  
}

@media (max-width:480px) {
    .about-para {
        text-align: justify ;
        text-justify:inter-word;
    }
}

.about-us-container p {
    font-size: 1.2rem;
    font-weight: 500;
}