.heading {
    color: #4b1827;
    font-weight: bold;
}

.footer-container {
    padding: 5% 2% 3%;
    color: black;
    font-weight: 300;
}

.contact-address{
    padding-bottom: 5%;
    
}

.select-text {
    user-select: text;
}

.select-text:hover{
    cursor: text;
}

.useful-link-text{
    font-size: 13.5px !important;
    color: black;
    text-decoration: none;
}
.useful-link-text:hover{
    color: #808080;
    cursor: pointer;
}

.tileMe {
    display: inline;
    float: left;
    padding-right: 0.4rem;
    transition: all .2s ease;
}

.tileMe:hover {
    color: #7e2b44 !important;
}

.lower-footer{
    border:solid;
    background-color: #4b1827;
}
.copyright{
    color: #eebc79;
    text-align: center;
    margin: 0%;
    padding: 1.5% 0;
}

@media (max-width:480px){
    .copyright{
        font-size: 12px;
    }
}